export default function errorMessage({ title, zipcode, detailAddress, officialname, belong, information, dday }) {
  const errors = {};

  if (!title) {
    errors.title = "  .";
  }

  if (!zipcode) {
    errors.zipcode = "  .";
  }

  if (!detailAddress) {
    errors.detailAddress = "  .";
  }

  if (!officialname) {
    errors.officialname = "   .";
  }

  if (!belong) {
    errors.belong = "   .";
  }

  if (!information) {
    errors.information = "  .";
  }

  if (!dday) {
    errors.dday = " .";
  }
  return errors;
}