export default function validate({ username, password, password2, nickname }) {
  const errors = {};

  if (!username) {
    errors.username = "  .";
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(username)) {
    errors.username = "   .";
  }

  if (!password) {
    errors.password = "  .";
  } else if (password.length < 8) {
    errors.password = "8    .";
  }

  if (!password2) {
    errors.password2 = "  .";
  } else if (password2.length < 8) {
    errors.password2 = "8    .";
  }
  
  if (!nickname) {
    errors.nickname = "  .";
  } else if (nickname.length < 2) {
    errors.nickname = '2    .'
  }
  return errors;
}