/* eslint-disable no-unused-vars */
import '../style/SignIn.scss';
import { useState } from 'react';
import InputCom from '../Components/InputCom';
import useForm from '../Hooks/useForm';
import validate from '../Components/validate';
import axios from 'axios';
import apiSwagger from '../apiSwagger/apiSwagger.json'

export default function SignIn() {
  // ,   
  const { values, errors, handleChange, handleSubmit } = useForm({
    initialValues: { username: '', password: '', nickname: '', password2: '' },
    onSubmit: () => {},
    validate
  })

  const [noMatchPassword, setNoMatchPassword] = useState(false);
  const [passwordCheck, setPasswordCheck] = useState('');

  const handlePasswordChk = e => {
    setNoMatchPassword(e.target.value !== values.password);
    setPasswordCheck(e.target.value);
  };

  //  
  const [btnName, setBtnName] = useState('')

  const handleNickname = async () => {
    try {
      const loadAxios = await axios.post(`${apiSwagger.url}:${apiSwagger.port}/${apiSwagger.api}/nicknamecheck/`, {
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ "nickname": values.nickname })
      })
      if (loadAxios.data.message === 'possible nickname') {
        if (values.nickname === '') {
          alert(' .')
        } else {
          alert('  .')
          setBtnName('')
        }
      } else if (loadAxios.data.message === 'already exists') {
        alert('  .')
      }
    }
    catch (error) {
    }
  }

  const changeBtnName = e => {
    if (e.target.value !== values.nickname) {
      setBtnName('')
    }
  }

  return (
    <main id="sign-main">
      <div className="container">
        <h2 className='h2'></h2>
        <section className='section container'>
          <form onSubmit={handleSubmit}>
            <div className='email-input form-common'>
              <InputCom
                title=""
                necessary='*'
                name="username"
                type="text"
                class="text-input longWidth"
                placeholder=" ."
                event={handleChange}
                value={values.username}
              />
              {errors.username && <p style={{ color: 'red' }}>{errors.username}</p>}
            </div>
            <div className='password-input form-common'>
              <InputCom
                title=""
                necessary='*'
                name="password"
                type="password"
                class="text-input longWidth"
                placeholder=" ."
                event={handleChange}
              />
              {errors.password && <p style={{ color: 'red' }}>{errors.password}</p>}
            </div>
            <div className='password-input form-common' onChange={handlePasswordChk}>
              <InputCom
                title=" "
                necessary='*'
                name="password2"
                type="password"
                class="text-input longWidth"
                placeholder=" ."
                event={handleChange}
              />
              {noMatchPassword && <p style={{ color: 'red' }}>  .</p>}
              {errors.password2 && <p style={{ color: 'red' }}>{errors.password2}</p>}
            </div>
            <div className='nickname-input form-common' onChange={changeBtnName}>
              <InputCom
                title=""
                necessary='*'
                name="nickname"
                type="text"
                class="text-input nickname"
                placeholder=" ."
                event={handleChange}
                value={values.nickname}
              />
              <button type='button' className='confirmBtn' onClick={handleNickname}>{btnName}</button>
              {errors.nickname && <p style={{ color: 'red' }}>{errors.nickname}</p>}
            </div>

            <p className='infoText'>     . <br />   .</p>
            <button type='submit' className='joinComplete' disabled={btnName === ''} ></button>
          </form>
        </section>
      </div>
    </main>
  )
}